<template>
    <form class="row" @submit.prevent="submit()" >
        
        <div class="col-lg-12">
            <div class="card">
                
                <div class="card-body" >
                    <div class="row">
                        <div class="col-md-12">
                            <h3>Cadastro de Centro de Custo <span v-if="form.id">: {{form.nome}}</span> </h3>
                        </div>
                    </div>
                    <hr>
                    <div class="row">
                        
                        <div class="col-md-12">
                            
                            <div class="row">
                                <div class="col-md-12">
                                    <h4>Dados Cadastrais</h4>
                                </div>
                            </div>
                            <hr>
                            <div class="row">
                                <label class="col-sm-3 col-form-label" v-show="form.id">Id</label>
                                <div class="col-sm-9" v-show="form.id">
                                    <input-componente
                                        :disabled="true"
                                        v-model="form.id"                            
                                        />
                                </div>

                                <!-- <label class="col-sm-3 col-form-label" >Centros de Custo Pai</label>
                                <div class="col-sm-9" >
                                    <input type="text" :disabled="true" value="centroPai">
                                </div> -->
                                <!--<div class="col-sm-9" >
                                    <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                                        <thead>
                                        <tr>
                                            <th colspan="2">Centros de Custo</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="registro in centros" :key="registro.id">
                                                <td class="text-right">{{registro.codigo}}</td>
                                                <td>{{registro.nome}}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div> -->

                                <label class="col-sm-3 col-form-label">Codigo</label>
                                <div class="col-sm-9">
                                    <input-componente
                                        :mask="['#', '#.##','#.##.###']"
                                        v-model="form.codigo"
                                        :validator="$v.form.codigo"
                                        @input="$v.form.codigo.$touch"
                                        />
                                </div>

                                <label class="col-sm-3 col-form-label">Nome</label>
                                <div class="col-sm-9">
                                    <input-componente
                                        v-model="form.nome"
                                        :validator="$v.form.nome"
                                        @input="$v.form.nome.$touch"
                                        />
                                </div>


                                <label class="col-sm-3 col-form-label">Descrição</label>
                                <div class="col-sm-9">
                                    <input-componente
                                        v-model="form.descricao"
                                        :validator="$v.form.descricao"
                                        @input="$v.form.descricao.$touch"
                                        />
                                </div>

                                <label class="col-sm-3 col-form-label">Ativo:</label>
                                <div class="col-sm-9">
                                    <container-componente :validator="$v.form.ativo">
                                        <div class="custom-control custom-checkbox mb-3">
                                            <input type="checkbox" class="custom-control-input" id="customCheckativo" v-model="form.ativo">
                                            <label class="custom-control-label" for="customCheckativo">{{form.ativo ? 'Ativo' : 'Inativo'}}</label>
                                        </div>
                                    </container-componente>
                                </div>
                            </div>     
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12 text-center" v-if="erros || sucesso">
                        <mensagem-componente :titulo="errosTitulo" v-if="erros" :erros="erros" @fechar="erros = null"></mensagem-componente>
                        <mensagem-componente v-if="sucesso" :sucesso="sucesso" @fechar="sucesso = null"></mensagem-componente>
                    </div>
                    <div class="col-md-12 button-items text-center">

                        <router-link class="btn btn-warning waves-effect waves-light w-sm" title="Voltar" :to="{name: 'centroCustosLista'}">
                            <i class="bx bx-arrow-back d-block font-size-16"></i> Voltar
                        </router-link>

                        <button type="submit" class="btn btn-primary waves-effect waves-light w-sm" title="salvar">
                            <i class="bx bxs-save d-block font-size-16"></i> Salvar
                        </button>

                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12 text-right" v-if="form.id">
                        <log-componente :index="form.id" subject_type="App\Models\Cliente\CentroCusto"  :subject_id="form.id" ></log-componente>
                    </div>
                </div>
            </div>
        </div>        
    </form>
</template>

<script>

import { required,  maxLength, } from "vuelidate/lib/validators";
import { CentroCustoService } from '../../../services'



const centroCustoService = new CentroCustoService();

export default {
  data() {
        return {
            form: {
                id: null,
                codigo: '',
                nome: '',
                descricao: '',
                ativo: true,
            },
          
            sucesso: null,
            erros:null,
            errosTitulo:'',
              
            timer: null,
            centros: [],
            cidade:{},
            

        } ;
    },
    validations() {
        return {
            form: {
                codigo: { required, maxLength:maxLength(20)},
                nome: { required, maxLength:maxLength(50)},
                descricao: { required, maxLength:maxLength(200)},
                ativo: { required },
                centro_custo_id: {}
            },
        }
        

    },
    mounted() {
        if (this.$route.params.id) {
            this.buscarObjeto(this.$route.params.id);
        }       
        setTimeout(() => {
            this.buscarCentros();
        }, 400);
    },
    methods: {

        submit: function(){
            this.$v.form.$touch();
            this.erros = null;
            this.sucesso = null;    
            this.errosTitulo ='';

            if (this.$v.form.$invalid){
                this.errosTitulo = 'Preencha o formulário corretamente, alguns campos estão com erros!'
                this.erros = this.$v.form;
                return false;
            }
            let obj = {...this.form};
            
            let request = null;
            if(obj.id)    
                request = centroCustoService.atualizar(obj);
            else
                request = centroCustoService.criar(obj);

            request.then(
                response => {
                    let redireciona = !this.form.id;
                    this.form = response.data;
                    this.sucesso = "Registro salvo com sucesso!";
                    if(redireciona) {
                        let index = String(this.$route.path).lastIndexOf("/");
                        let newPath = String(this.$route.path).slice(0, index);
                        newPath += "/" + this.form.id;
                        this.$router.push(newPath);
                        this.buscarObjeto(this.form.id);
                    }
                }   
            ).catch( erro => this.erros = erro )

        },

        buscarObjeto: function(id){
            centroCustoService.buscar(id)
                .then(  response => {
                    this.form = response.data;
                }).catch( erro => this.erros = erro)
        },

        buscarCentros: function(){
            centroCustoService.listar({ordenar: 'codigo', ordem: 'asc', per_page:100})
                .then(  response => {
                    this.centros = response.data.data;
                }).catch( erro => this.erros = erro)
        },


    // },
    // // computed: {
    // //     centroPai(){
            
    //     }
    }
};
</script>
